// use t3kit variables based on bootstrap variables
// check "theme/src/styles/_variables.scss"
@use '../../variables' as var;
// use bootstrap mixins based on t3kit variable overrides
// check "theme/src/styles/bootstrap/mixins/_index.scss"
// @use '../../bootstrap/mixins' as mixin;

.big-icon-text-btn__wrp {
    position: relative;
    overflow: auto;
    padding: 0 15px;
  margin: 0 -15px;
}

.big-icon-text-btn {
    text-align: center;
    padding: 50px 0 40px;
}

.big-icon-text-btn__link {
    // displays: flex;
    position: relative;
    z-index: 1;

    &:after {
        // @include icons();

        position: relative;
        display: inline-block;
        font-size: 20px;
        margin-left: 8px;
        padding: 0 3px;
        top: 1px;
    }
}

.big-icon-text-btn__p {
    margin-bottom: 18px;
}

.big-icon-text-btn__icon {
    padding: 30px 29px 30px 31px;
    border: 2px solid tint(var.$primary, 70%);
    color: var.$primary;
    border-radius: 55px;
    margin-right: 0;
    margin-bottom: 10px;
    font-size: 46px;
    display: inline-block;
}

.big-icon-text-btn__sham-link {
    color: var.$primary;

    &:after {
        // @include icons();

        position: relative;
        display: inline-block;
        font-size: 20px;
        margin-left: 8px;
        padding: 0 3px;
        top: 1px;
    }
}

.big-icon-text-btn._whole-area-link .big-icon-text-btn__link {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    font-size: 0;
    text-indent: -9999px;
    border: none;
    background: transparent;
    z-index: 1;

    &:active {
        box-shadow: none;
    }
}

.frontend_editing .big-icon-text-btn._whole-area-link .big-icon-text-btn__link {
    z-index: -1;
}

.big-icon-text-btn._whole-area-link:hover .big-icon-text-btn__link {
    background-color: fade(#fff, 10%);
}
