// use t3kit variables based on bootstrap variables
// check "theme/src/styles/_variables.scss"
@use '../../variables' as var;
// use bootstrap mixins based on t3kit variable overrides
// check "theme/src/styles/bootstrap/mixins/_index.scss"
@use '../../bootstrap/mixins' as mixin;

// =================================
// === Footer  =====================
// =================================
.footer {
  background: var.$gray-900;
  padding: 140px 0 60px;
  font-family: var.$font-family-sans-serif;
  font-weight: var.$font-weight-medium;
  font-size: 14px;
  color: var.$white;
  -webkit-font-smoothing: antialiased;
  text-transform: uppercase;

  & a {
    transition: color 0.2s ease;
    color: var.$white;

    &:hover,
    &:focus {
      color: var.$gray-500;
      text-decoration: none;
    }
  }

  @include mixin.media-breakpoint-down(md) {
    text-align: center;
  }
}

.footer__header {
  color: var.$white;
  font-weight: var.$font-weight-bolder;
  letter-spacing: 0.2px;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  text-transform: uppercase;
}

// -----------------------------------------------------------------------------
// -----------------------------------------------------------------------------

// =================================
// === Footer Middle ===============
// =================================
.footer__middle {
  border-bottom: 1px solid rgba(var.$white, 0.1);
  margin-bottom: 40px;

  @include mixin.media-breakpoint-up(lg) {
    margin-bottom: 50px;
    padding-bottom: 83px;
  }

  & p {
    line-height: 30px;
    margin-bottom: 0;

    @include mixin.media-breakpoint-down(md) {
      line-height: 50px;
      margin-bottom: 0;
    }
  }
}

.footer__middle-column {
  @include mixin.media-breakpoint-down(md) {
    margin-bottom: 67px;
  }

  @include mixin.media-breakpoint-between(sm, lg) {
    &:nth-child(1),
    &:nth-child(2) {
      margin-bottom: 120px;
    }
  }
}

// === Footer Logo===========
// =================================

.footer__logo {
  margin-bottom: 34px;
}

.footer__logo-image {
  max-width: 175px;
}

// === Footer Links===========
// =================================
.footer__links-list {
  list-style: none;
  padding-left: 0;
}

.footer__links-item {
  line-height: 50px;
}
