.footer__form-input {
  height: 56px;
  outline: none;
  border: none;
  border-radius: 0;
  background-color: rgba(var.$gray-600, 0.5);
  color: var.$white;
  box-shadow: none;
  padding: 17px 19px;

  &::-webkit-input-placeholder,
  &::-moz-placeholder,
  &:-ms-input-placeholder,
  &:-moz-placeholder {
    color: rgba(var.$white, 0.5);
  }

  &:focus {
    outline: none;
    border: none;
    background-color: rgba(var.$gray-600, 0.5);
    color: var.$white;
    box-shadow: none;
  }
}

.footer__btn {
  border: 4px solid var.$white;
  font-size: 16px;
  height: 61px;
  margin-top: 34px;
  width: 100%;
  font-weight: var.$font-weight-bolder;
  letter-spacing: 0.2px;
  -webkit-font-smoothing: antialiased;
  text-transform: uppercase;
  background-color: var.$gray-900;

  &:hover {
    background-color: var.$gray-800;
  }
}
// -----------------------------------------------------------------------------
// -----------------------------------------------------------------------------
// === Footer Social Links ===============
// =================================

.footer__social__item {
  color: var.$white;
  margin-left: 50px;

  &:first-child {
    margin-left: 0;
  }
}

// === Footer Bottom ===============
// =================================
.footer__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mixin.media-breakpoint-down(md) {
    flex-direction: column;
  }
}

.footer__bottom-wrap {
  display: flex;

  @include mixin.media-breakpoint-down(md) {
    margin-bottom: 50px;
  }

  @include mixin.media-breakpoint-down(sm) {
    flex-wrap: wrap;
  }
}

.footer__bottom-links {
  margin-bottom: 0;

  @include mixin.media-breakpoint-down(sm) {
    width: 100%;
  }
}

.footer__bottom-copyright {
  line-height: unset;
  margin-bottom: 0;

  @include mixin.media-breakpoint-down(sm) {
    width: 100%;
    margin-bottom: 10px;
  }
}

.footer__bottom-item {
  margin-left: 20px;
  position: relative;
  line-height: unset;
  display: inline-block;

  &:after {
    content: "";
    background-color: #fff;
    width: 1px;
    height: 12px;
    position: absolute;
    top: 4px;
    left: -11px;
  }

  @include mixin.media-breakpoint-down(sm) {
    margin: 0 10px 10px;

    &:first-child:after {
      display: none;
    }
  }
}

